nav {
  position: fixed;
  z-index: 1;
  top: 0;
  width: 100%;
  height: 50px;
  background: #f12711; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    45deg,
    #f5af19,
    #f12711
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    45deg,
    #f12711,
    #f5af19
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.liste {
  background: #f12711; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    45deg,
    #f12711,
    #f5af19
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    45deg,
    #f12711,
    #f5af19
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  list-style-type: none;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  font-size: 22px;
}

.items {
  color: #f1f1f1;
  cursor: pointer;
  margin: 0 15px;
}

.items a {
  color: #f1f1f1;
  text-decoration: none;
}

.items a:hover {
  text-decoration: underline;
}

.btn {
  position: absolute;
  top: 7px;
  right: 10px;
  padding: 5px 10px;
  color: #000;
  font-size: 18px;
  display: none;
}

@media screen and (max-width: 500px) {
  .liste {
    flex-direction: column;
    height: auto;
  }
  .items:nth-child(1) {
    border-top: 1px solid rgba(255, 255, 255, 0.6);
    margin-top: 50px;
  }
  .items {
    height: 70px;
    width: 100%;
    border-bottom: 1px solid rgba(255, 255, 255, 0.6);
    line-height: 70px;
    text-align: center;
  }
  .btn {
    display: block;
  }
}
