form {
  display: flex;
  flex-direction: column;
  text-align: left;
}

label {
  font-size: 1.2em;
  margin-bottom: 0.3em;
}

input {
  font-size: 1.1em;
  margin-bottom: 1.5em;
  padding: 10px;
  font-family: Arial, Helvetica, sans-serif;
}

.btn-add-contact {
  display: block;
  margin: 0.8em auto;
  font-size: 1.2em;
  color: #fff;
  border: none;
  background: rgba(245, 175, 25, 0.7); /* fallback for old browsers */
  background: -webkit-linear-gradient(
    45deg,
    rgba(245, 175, 25, 0.7),
    rgba(241, 39, 17, 0.7)
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    45deg,
    rgba(245, 175, 25, 0.7),
    rgba(241, 39, 17, 0.7)
  );
}
