.contact-card-container {
  min-width: 400px;
  max-width: 400px;
  min-height: 350px;
  margin: 10px;
  padding: 30px 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.user-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  margin-bottom: 1.5em;
  font-size: 1.1em;
}

.contact-card-container:nth-child(odd) .card-user-round {
  background: rgba(245, 175, 25, 0.7); /* fallback for old browsers */
  background: -webkit-linear-gradient(
    45deg,
    rgba(245, 175, 25, 0.7),
    rgba(241, 39, 17, 0.7)
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    45deg,
    rgba(245, 175, 25, 0.7),
    rgba(241, 39, 17, 0.7)
  );
}
.contact-card-container:nth-child(even) .card-user-round {
  background: rgba(241, 39, 17, 0.7); /* fallback for old browsers */
  background: -webkit-linear-gradient(
    45deg,
    rgba(241, 39, 17, 0.7),
    rgba(245, 175, 25, 0.7)
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    45deg,
    rgba(241, 39, 17, 0.7),
    rgba(245, 175, 25, 0.7)
  );
}

.card-user-round {
  width: 200px;
  height: 200px;
  margin-bottom: 1em;
  border-radius: 50%;
  font-size: 1.5em;
  position: relative;
  color: #fff;
}

.card-user-round p {
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  word-break: break-all;
  width: 75%;
}

.contact-details-link {
  align-self: end;
  margin-right: 1em;
}
