.contact-content {
  min-width: 300px;
  max-width: 600px;
  margin: 150px auto;
  padding: 0 2em;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.contact-content h2 {
  font-size: 3em;
  margin-bottom: 20px;
}

.detail-user-round {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  font-size: 1.2em;
  position: relative;
  color: #fff;
  box-shadow: 7px 5px 15px rgba(0, 0, 0, 0.2);
  background: rgba(245, 175, 25, 0.7); /* fallback for old browsers */
  background: -webkit-linear-gradient(
    45deg,
    rgba(245, 175, 25, 0.7),
    rgba(241, 39, 17, 0.7)
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    45deg,
    rgba(245, 175, 25, 0.7),
    rgba(241, 39, 17, 0.7)
  );
}

.detail-user-round p {
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  word-break: break-all;
  width: 75%;
}

.detail-user-info {
  text-align: left;
}

.detail-user-info div {
  margin: 1.5em 0;
}

.detail-name {
  font-size: 1.5em;
}
